import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoading:false,
    refCount: 0,
    sportsmen:[]
  },
  mutations: {
    loading(state, isLoading) {
      console.log({isLoading})
      if (isLoading) {
        state.refCount++;
        state.isLoading = true;
      } else if (state.refCount > 0) {
        state.refCount--;
        state.isLoading = (state.refCount > 0);
      }
    },
  },
  actions: {
    getSportsmen({state},cre){
      axios.get('v1/api/sportsman?page='+cre)
      .then(res=>{
        state.sportsmen=res.data.data
      })
    },
    createSportSmen(_,cre){
      axios.post('v1/api/sportsman/',cre,
          {
            headers: {
              'Authorization': 'Bearer ' + localStorage.getItem('access'),
              'Accept-Language':'ru'
            }
          }
          )
    },
    editSportSmen(_,cre){
      axios.post('v1/api/sportsman/',cre,
          {
            headers: {
              'Authorization': 'Bearer ' + localStorage.getItem('access'),
              'Accept-Language':'ru'
            }
          }
      )
    }

  },
  modules: {
  }
})
