<script>

import Destricts from '@/Json/districts.json'
import Regions from '@/Json/regions.json'
import Kvartals from '@/Json/kvartals.json'
import Sidebar from "../components/Sidebar";

export default {
  name: 'Home',
  components: {
    Sidebar
  },
  data(){
    return{
      search:'',
      sportsman:[],
      city:[],
      region:[],
      destricts:[]
    }
  },
  mounted() {
    this.city=Regions
    this.region=Destricts
    this.destricts=Kvartals

    this.$axiosDefault.get(`admin-sportsman?page=${1}`, {
      headers: {
        'Authorization': 'Bearer ' + localStorage.getItem('access'),
        'Accept-Language':'ru'
      }
    })
    .then(res=>{
      this.sportsman=res.data
    })
  },
  methods:{
    clickCallback: function (pageNum) {

      this.$axiosDefault.get(`admin-sportsman?page=${pageNum}`, {
        headers: {
          'Authorization': 'Bearer ' + localStorage.getItem('access'),
          'Accept-Language': 'uz-latin'
        }
      })
          .then(res=>{
            this.sportsman=res.data
          })
    },
    logOut(){
      localStorage.removeItem('access')
      this.$router.go('/login')

      localStorage.removeItem('access')
      localStorage.removeItem('refresh')
      localStorage.removeItem('avatar')
      localStorage.removeItem('email')
      localStorage.removeItem('full_name')
      localStorage.removeItem('username')
    },
    deletes(item) {

      let isBoss = confirm("You really want to delete?");
      if (isBoss == true) {
        this.$axiosDefault.delete('admin-sportsman/' + item + '/',
            {
              headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('access'),
                'Accept-Language': 'uz-latin'
              }
            })
            .then(() => {
              this.$router.go('/')
            })

      }

    },
    searchFilt(){
      console.log(this.search)
      this.$axiosDefault.get(`sportsman?search=${this.search}`, {
        headers: {
          'Authorization': 'Bearer ' + localStorage.getItem('access'),
          'Accept-Language': 'uz-latin'
        }
      })
      .then(res=>{
        this.sportsman=res.data
      })
    }

  },
}
</script>
